import React from 'react'
import {useState, useEffect} from 'react'
import { Link, useLocation } from 'react-router-dom';
import {toAbsoluteUrl} from '../../../_metronic/helpers'
import { NavLink } from 'react-router-dom';
import { faSparkles } from '@fortawesome/pro-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import image1 from './image1.png'
import image2 from './image2.png'
import image3 from './image3.png'
import image4 from './image4.png'
import image5 from './image5.png'

const Feedstab=()=>{
    const location = useLocation();

    const data = [
        {
          id: 1,
          title: 'Kitchen 2 - Cap Monitor',
          content: 'Employee is not wearing kitchen cap.',
          iconUrl: '/media/avatars/critical-black.png',
          imgUrl: image1,
          priority: 'critical',
          ratingUrl: 5,
          tags: ['#AndhraKaaramKitchen2', '#Critical'],
        },
        {
          id: 2,
          title: 'Entrance Mat Monitor',
          content: 'Mat is not present at the entrance',
          iconUrl: '/media/avatars/low.png',
          imgUrl: image2,
          priority: 'low',
          ratingUrl: 2,
          tags: ['#AndhraKaaramEntrance', '#Low'],
        },
        {
          id: 3,
          title: 'Kitchen 3 - Wet Floor Monitor',
          content: 'Floor is wet and slippery',
          iconUrl: '/media/avatars/medium.png',
          imgUrl: image3,
          priority: 'medium',
          ratingUrl: 3,
          tags: ['#AndhraKaaramKitchen3', '#Medium'],
        },
        {
          id: 4,
          title: 'Kitchen 2 - Unattended Active Stove Monitor',
          content: 'Stove is in operation without any dish present and there is no active monitoring',
          iconUrl: '/media/avatars/critical-black.png',
          imgUrl: image4,
          priority: 'critical',
          ratingUrl: 5,
          tags: ['#AndhraKaaramKitchen2', '#Critical'],
        },
        {
          id: 5,
          title: 'Unattended Cash Counter Monitor',
          content: 'Cash counter is unattended',
          iconUrl: '/media/avatars/low.png',
          imgUrl: image5,
          priority: 'low',
          ratingUrl: 1,
          tags: ['#AndhraCPIPCam', '#Low'],
        },
      ]

    return(
    <div className="d-flex flex-column flex-column-fluid">

    <div className='row g-5 g-xxl-8'>
      <div className='col-xl-6'>
        {data.slice(0, Math.ceil(data.length / 2)).map((card) => (
          <FeedCard
            key={card.id}
            title={card.title}
            content={card.content}
            iconUrl={card.iconUrl}
            imgUrl={card.imgUrl}
            ratingUrl={card.ratingUrl}
            tags={card.tags}
            image={card.image}
          />
        ))}
      </div>
      <div className='col-xl-6'>
        {data.slice(Math.ceil(data.length / 2)).map((card) => (
          <FeedCard
            key={card.id}
            title={card.title}
            content={card.content}
            iconUrl={card.iconUrl}
            imgUrl={card.imgUrl}
            ratingUrl={card.ratingUrl}
            tags={card.tags}
            image={card.image}
          />
        ))}
      </div>
    </div>         

    </div>
     
    )
}

function FeedCard({title, content, imgUrl, iconUrl, ratingUrl, tags, image}) {
    const [showComments, setShowComments] = useState(false)
    const cardStyle = {
      marginBottom: '1.5rem',
      borderTop: ratingUrl === 5 ? '4px solid red' : 'none',
      borderBottom: ratingUrl === 5 ? '2px solid red' : 'none',
    }
    // #009EF7
    return (
      <div className='card card-flush' style={cardStyle}>
        {/*begin::Card header*/}
  
        <div className='d-flex card-header pt-9 align-items-center'>
          {/*begin::Author*/}
          <div className='symbol symbol-50px me-5'>
            <img src={toAbsoluteUrl(iconUrl)} className='h-50 align-self-center' alt='' />
          </div>
          {/* <i className="ki-duotone ki-profile-circle fs-1 text-muted me-3" >
                              <span className="path1" />
                              <span className="path2" />
                              <span className="path3" />
                            </i> */}
          {/*end::Avatar*/}
          <div className='flex-grow-1'>
            <div className='d-flex justify-content-between align-items-center'>
              <div>
                {/*begin::Name*/}
                <NavLink to='/dashboard/monitoring-dashboard' className='text-gray-800 text-hover-primary fs-4 fw-bold'>
                  {title}
                </NavLink>
  
                {/* <br /> */}
  
                {/* <div className='symbol symbol-70px '>
                  <img src={toAbsoluteUrl(ratingUrl)} className='h-50 align-self-center' alt='' />
                </div> */}
  
                {/* <StarRating rating={ratingUrl} /> */}
                <div>
                  {/* <FontAwesomeIcon icon={faSparkles} size='lg' style={{color: "#f9c406", paddingRight: "0 2px"}}/>
                                <FontAwesomeIcon icon={faSparkles} size='lg' style={{color: "#f9c406", padding: "0 2px"}}/>
                                <FontAwesomeIcon icon={faSparkles} size='lg' style={{color: "#f9c406", padding: "0 2px"}}/>
                                <FontAwesomeIcon icon={faSparkles} size='lg' style={{color: "#f9c406", padding: "0 2px"}}/>
                                <FontAwesomeIcon icon={faSparkles} size='lg' style={{color: "#f9c406", padding: "0 2px"}}/> */}
                  <RatingStars rating={ratingUrl} />
                </div>
                {/* <div className='rating'>
                          <div className='rating-label me-2 checked'>
                            <i className='bi bi-star-fill fs-7'></i>
                          </div>
                          <div className='rating-label me-2 checked'>
                            <i className='bi bi-star-fill fs-7'></i>
                          </div>
                          <div className='rating-label me-2 checked'>
                            <i className='bi bi-star-fill fs-7'></i>
                          </div>
                          <div className='rating-label me-2 checked'>
                            <i className='bi bi-star-fill fs-7'></i>
                          </div>
                          <div className='rating-label me-2 checked'>
                            <i className='bi bi-star-fill fs-7'></i>
                          </div>
                        </div> */}
  
                {/*end::Name*/}
                {/*begin::Date*/}
                <span className='text-gray-400 fw-semibold d-block'>Yesterday at 5:06 PM</span>
                {/*end::Date*/}
              </div>
              {/*begin::Card toolbar*/}
              <div className='card-toolbar'>
                {/*begin::Menu toggle*/}
                <button
                  className='btn btn-icon btn-color-gray-400 btn-active-color-primary me-n4'
                  data-kt-menu-trigger='click'
                  data-kt-menu-placement='bottom-end'
                  data-kt-menu-overflow='true'
                >
                  <i className='ki-duotone ki-dots-square fs-1'>
                    <span className='path1' />
                    <span className='path2' />
                    <span className='path3' />
                    <span className='path4' />
                  </i>
                </button>
                {/*end::Menu toggle*/}
                {/*begin::Menu 2*/}
                <div
                  className='menu menu-sub menu-sub-dropdown menu-column menu-rounded menu-gray-800 menu-state-bg-light-primary fw-semibold w-200px'
                  data-kt-menu='true'
                >
                  {/*begin::Menu item*/}
                  <div className='menu-item px-3'>
                    <div className='menu-content fs-6 text-dark fw-bold px-3 py-4'>Quick Actions</div>
                  </div>
                  {/*end::Menu item*/}
                  {/*begin::Menu separator*/}
                  <div className='separator mb-3 opacity-75' />
                  {/*end::Menu separator*/}
                  {/*begin::Menu item*/}
                  <div className='menu-item px-3'>
                    <a href='#' className='menu-link px-3'>
                      Save Post
                    </a>
                  </div>
                  {/*end::Menu item*/}
                  {/*begin::Menu item*/}
                  <div className='menu-item px-3'>
                    <a href='#' className='menu-link px-3'>
                      Hide Post
                    </a>
                  </div>
                  {/*end::Menu item*/}
                  {/*begin::Menu item*/}
                  {/*end::Menu item*/}
                  {/*begin::Menu item*/}
                  <div className='menu-item px-3'>
                    <a href='#' className='menu-link px-3'>
                      Turn on notifications
                    </a>
                  </div>
                  {/*end::Menu item*/}
                  {/*begin::Menu separator*/}
                  <div className='separator mt-3 opacity-75' />
                </div>
                {/*end::Menu 2*/}
              </div>
              {/*end::Card toolbar*/}
            </div>
          </div>
          {/*end::Author*/}
        </div>
  
        {/*end::Card header*/}
        {/*begin::Card body*/}
        <div className='card-body'>
          {/*begin::Post content*/}
          <div className='fs-6 fw-normal text-gray-700'>{content}</div>
          <ul className='nav py-3 mx-0 mb-0 pb-0 my-0 px-0'>
            {/*begin::Item*/}
            <li className='nav-item pr-2'>
              {/* <a
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary btn-active-light-primary fw-bold px-4 me-1 collapsible'
                  data-bs-toggle='collapse'
                  href='#kt_social_feeds_comments_2'
                >
                  #AnomalyDetection
                </a> */}
              <span className='fs-7 p-2 badge badge-light-primary'>{tags[0]}</span>
            </li>
            {/*end::Item*/}
            {/*begin::Item*/}
            <li className='nav-item px-2'>
              {/* <a
                  href='#'
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary fw-bold px-4 me-1'
                >
                  #AnomDetect
                </a> */}
              <span className='fs-7 p-2 badge badge-light-primary'>{tags[1]}</span>
            </li>
            {/*end::Item*/}
            {/*begin::Item*/}
            <li className='nav-item px-1'>
              {/* <a
                  href='#'
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary fw-bold px-4'
                > 
                </a>*/}
  
              <span className='fs-7 p-2 badge badge-light-primary'>{tags[2]}</span>
            </li>
            {/*end::Item*/}
          </ul>
          <div className='row g-3 g-lg-6 pt-5'>
                  {/*begin::Col*/}
                  <div className='col-md-12'>
                      {/*begin::Img*/}
                      <a href=''>
                        {image != '' ? <img src={imgUrl} className='rounded w-100' style={{height: '370px'}} alt='' /> : ''}
                      
                      </a>
                      {/*end::Img*/}
                  </div>
                  {/*end::Col*/}
                  </div>
          {/*end::Post content*/}
        </div>
        {/*end::Card body*/}
        {/*begin::Card footer*/}
        <div className='card-footer pt-0 mt-0'>
          {/*begin::Info*/}
          <div className='mb-6'>
            {/*begin::Separator*/}
            <div className='separator separator-solid' />
            {/*end::Separator*/}
            {/*begin::Nav*/}
            <ul className='nav py-3'>
              {/*begin::Item*/}
              <li className='nav-item'>
                <a
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary btn-active-light-primary fw-bold px-4 me-1 collapsible'
                  data-bs-toggle='collapse'
                  href='#kt_social_feeds_comments_2'
                >
                  <i className='ki-duotone ki-dislike fs-2 me-1'>
                    <span className='path1' />
                    <span className='path2' />
                    <span className='path3' />
                  </i>
                  Dislike
                </a>
              </li>
              {/*end::Item*/}
              {/*begin::Item*/}
              <li className='nav-item'>
                <a
                  href='#'
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary fw-bold px-4 me-1'
                >
                  <i className='ki-duotone ki-like fs-2 me-1'>
                    <span className='path1' />
                    <span className='path2' />
                  </i>
                  Like
                </a>
              </li>
              {/*end::Item*/}
              {/*begin::Item*/}
              <li className='nav-item'>
                <a
                  href='#'
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary fw-bold px-4'
                >
                  <i className='ki-duotone ki-bookmark fs-2 me-1'>
                    <span className='path1' />
                    <span className='path2' />
                  </i>
                  Save
                </a>
              </li>
              {/*end::Item*/}
              {/*begin::Item*/}
              <li className='nav-item'>
                <a
                  href='#'
                  className='nav-link btn btn-sm btn-color-gray-600 btn-active-color-primary fw-bold px-4'
                  onClick={() => setShowComments(!showComments)}
                >
                  <i className='ki-duotone ki-file fs-2 me-1'>
                    <span className='path1' />
                    <span className='path2' />
                  </i>
                  {/* {showComments ? 'Hide' : 'Show'} Comments */}Comments
                </a>
              </li>
              {/*end::Item*/}
            </ul>
  
            {/*end::Nav*/}
            {/*begin::Separator*/}
            <div className='separator separator-solid mb-1' />
            {/*end::Separator*/}
            {/*begin::Comments*/}
  
            {/*end::Collapse*/}
          </div>
          {/*end::Info*/}
          {/*begin::Comment form*/}
  
          {showComments && (
            <div className='d-flex align-items-center'>
              <div className='position-relative w-100'>
                {/*begin::Input*/}
                <textarea
                  type='text'
                  className='form-control form-control-solid border ps-5'
                  rows={1}
                  name='search'
                  // value=""
                  data-kt-autosize='true'
                  placeholder='Write your comment..'
                  // defaultValue={""}
                />
              </div>
              {/*end::Input group*/}
            </div>
          )}
          {/*end::Comment form*/}
        </div>
        {/*end::Card footer*/}
      </div>
    )
  }


  const RatingStars = ({rating}) => {
    const yellowStars = Array.from({length: rating}, (_, index) => (
      <FontAwesomeIcon
        key={index}
        icon={faSparkles}
        size='lg'
        style={{color: '#f9c406', padding: '0 3px'}}
      />
    ))
  
    const greyStars = Array.from({length: 5 - rating}, (_, index) => (
      <FontAwesomeIcon
        key={index + rating}
        icon={faSparkles}
        size='lg'
        style={{color: '#c5c3ba', padding: '0 3px'}}
      />
    ))
  
    return (
      <div>
        {yellowStars}
        {greyStars}
      </div>
    )
  }

export default Feedstab;
