import React,{useState} from 'react';
import { useParams,useNavigate } from 'react-router-dom';
import { PluginHeader } from './PluginHeader';
import { toAbsoluteUrl } from '../../../_metronic/helpers';
import { useLocation } from 'react-router-dom';
import FeedsTab from '../monetary-dashboard/Feedstab';
import Descriptionmonteary from './Descriptionmonteary';
import PricingMonitoring from './pricingmonitoring';
import { useEffect } from 'react';
import { UsersListWrapper } from '../../modules/apps/viewmonitoringdashboard/users-list/UsersList';
import SettingsMonitoring from './settingsmonitoring';

function PluginPageSample() {
    const { tab = 'descriptionmonitoring' } = useParams();
    const location = useLocation();
    const [activeTab, setActiveTab] = useState(tab);
    const navigate = useNavigate();

    useEffect(() => {
        setActiveTab(tab);
      }, [tab]);
    
      // Function to handle tab change
      const handleTabChange = (newTab, event) => {
        event.preventDefault(); // Prevent default anchor behavior
        setActiveTab(newTab);
        window.history.pushState({}, '', `/plugin-page/${newTab}`); // Update the URL
      };
      
      useEffect(() => {
        // Update the URL with the current active tab when the component mounts
        navigate(`/plugin-page/${activeTab}`);
    }, [activeTab, navigate]);
     
    return (
        <>
        <div id="kt_app_toolbar" className="app-toolbar py-3 py-lg-6">
        {/*begin::Toolbar container*/}
        <div
            id="kt_app_toolbar_container"
            className="app-container container-xxl d-flex flex-stack"
        >
            {/*begin::Page title*/}
            <div className="page-title d-flex flex-column justify-content-center pluginpage-header me-3">
            {/*begin::Title*/}
            <h1 className="page-heading d-flex text-dark fw-bold fs-3 flex-column justify-content-center my-0">
                VizoAI Monitor
            </h1>
            {/*end::Title*/}
            {/*begin::Breadcrumb*/}
            <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 my-0 pt-1">
                {/*begin::Item*/}
                <li className="breadcrumb-item text-muted">
                <a
                    href="../../demo1/dist/index.html"
                    className="text-muted text-hover-primary"
                >
                    Home
                </a>
                </li>
                {/*end::Item*/}
                {/*begin::Item*/}
                <li className="breadcrumb-item">
                <span className="bullet bg-gray-400 w-5px h-2px" />
                </li>
                {/*end::Item*/}
                {/*begin::Item*/}
                <li className="breadcrumb-item text-muted">Main Dashboard</li>
                {/*end::Item*/}
            </ul>
            {/*end::Breadcrumb*/}
            </div>
            {/*end::Page title*/}
            {/*begin::Actions*/}

            {/*end::Actions*/}
        </div>
        {/*end::Toolbar container*/}
            </div>
            <div className="app-main flex-column flex-row-fluid" id="kt_app_main">
            {/*begin::Content wrapper*/}
            <div className="d-flex flex-column flex-column-fluid">
        {/*begin::Toolbar*/}
        
        {/*end::Toolbar*/}
        {/*begin::Content*/}
        <div id="kt_app_content" className="app-content flex-column-fluid">
        {/*begin::Content container*/}
        <div
            id="kt_app_content_container"
            className="app-container container-xxl widthheading"
            style={{paddingLeft: '2px', paddingRight: '2px'}}
        >
            {/*begin::Navbar*/}
            <div className="card mb-6 mb-xl-9">
            <div className="pluginpagepadding pt-9 pb-0">
                {/*begin::Details*/}
                <div className="d-flex pluginpage-header flex-sm-nowrap mb-6">
                {/*begin::Image*/}
                <div className="d-flex flex-center flex-shrink-0 bg-light rounded  h-100px w-lg-50px h-lg-50px pluginpageheadericon mb-4">
                    <img
                    className="pluginpageimgwidth mw-lg-65px"
                    src={toAbsoluteUrl("/media/avatars/monitorlogo.png")}
                    alt="image"
                    />
                </div>
                {/*end::Image*/}
                {/*begin::Wrapper*/}
                <div className="flex-grow-1">
                    {/*begin::Head*/}
                    <div className="d-flex justify-content-between align-items-start pluginpagedescription mb-2">
                    {/*begin::Details*/}
                    <div className="d-flex flex-column">
                        {/*begin::Status*/}
                        <div className="d-flex align-items-center mb-1">
                        <a
                            href="#"
                            className="text-gray-800 text-hover-primary fs-2 fw-bold me-3"
                        >
                        VizoAI Monitor
                        </a>
                        {/* <span className="badge badge-light-success me-auto">
                            Subscribed
                        </span> */}
                        </div>
                        {/*end::Status*/}
                        {/*begin::Description*/}
                        <div className="d-flex flex-wrap fw-semibold mb-4 pluginpageheadingsize text-gray-400">
                        #1 Tool to get started with Web Apps any Kind &amp; size
                        </div>
                        {/*end::Description*/}
                    </div>
                    {/*end::Details*/}
                    {/*begin::Actions*/}
                    <div className="d-flex mb-4">
                        {/* <a
                        href="#"
                        className="btn btn-sm btn-bg-light btn-active-color-primary me-3"
                        data-bs-toggle="modal"
                        data-bs-target="#kt_modal_users_search"
                        >
                        Add New Monitor
                        </a> */}
                        <a
                        href="#"
                        className="btn btn-sm btn-primary me-3"
                        data-bs-toggle="modal"
                        data-bs-target="#kt_modal_new_target"
                        >
                        Subscribe
                        </a>
                        {/*begin::Menu*/}
                        <div className="me-0">
                        <button
                            className="btn btn-sm btn-icon btn-bg-light btn-active-color-primary"
                            data-kt-menu-trigger="click"
                            data-kt-menu-placement="bottom-end"
                        >
                            <i className="ki-solid ki-dots-horizontal fs-2x" />
                        </button>
                        {/*begin::Menu 3*/}
                        <div
                            className="menu menu-sub menu-sub-dropdown menu-column menu-rounded menu-gray-800 menu-state-bg-light-primary fw-semibold w-200px py-3"
                            data-kt-menu="true"
                        >
                            {/*begin::Heading*/}
                            <div className="menu-item px-3">
                            <div className="menu-content text-muted pb-2 px-3 fs-7 text-uppercase">
                                Payments
                            </div>
                            </div>
                            {/*end::Heading*/}
                            {/*begin::Menu item*/}
                            <div className="menu-item px-3">
                            <a href="#" className="menu-link px-3">
                                Create Invoice
                            </a>
                            </div>
                            {/*end::Menu item*/}
                            {/*begin::Menu item*/}
                            <div className="menu-item px-3">
                            <a href="#" className="menu-link flex-stack px-3">
                                Create Payment
                                <span
                                className="ms-2"
                                data-bs-toggle="tooltip"
                                title="Specify a target name for future usage and reference"
                                >
                                <i className="ki-duotone ki-information fs-6">
                                    <span className="path1" />
                                    <span className="path2" />
                                    <span className="path3" />
                                </i>
                                </span>
                            </a>
                            </div>
                            {/*end::Menu item*/}
                            {/*begin::Menu item*/}
                            <div className="menu-item px-3">
                            <a href="#" className="menu-link px-3">
                                Generate Bill
                            </a>
                            </div>
                            {/*end::Menu item*/}
                            {/*begin::Menu item*/}
                            <div
                            className="menu-item px-3"
                            data-kt-menu-trigger="hover"
                            data-kt-menu-placement="right-end"
                            >
                            <a href="#" className="menu-link px-3">
                                <span className="menu-title">Subscription</span>
                                <span className="menu-arrow" />
                            </a>
                            {/*begin::Menu sub*/}
                            <div className="menu-sub menu-sub-dropdown w-175px py-4">
                                {/*begin::Menu item*/}
                                <div className="menu-item px-3">
                                <a href="#" className="menu-link px-3">
                                    Plans
                                </a>
                                </div>
                                {/*end::Menu item*/}
                                {/*begin::Menu item*/}
                                <div className="menu-item px-3">
                                <a href="#" className="menu-link px-3">
                                    Billing
                                </a>
                                </div>
                                {/*end::Menu item*/}
                                {/*begin::Menu item*/}
                                <div className="menu-item px-3">
                                <a href="#" className="menu-link px-3">
                                    Statements
                                </a>
                                </div>
                                {/*end::Menu item*/}
                                {/*begin::Menu separator*/}
                                <div className="separator my-2" />
                                {/*end::Menu separator*/}
                                {/*begin::Menu item*/}
                                <div className="menu-item px-3">
                                <div className="menu-content px-3">
                                    {/*begin::Switch*/}
                                    <label className="form-check form-switch form-check-custom form-check-solid">
                                    {/*begin::Input*/}
                                    <input
                                        className="form-check-input w-30px h-20px"
                                        type="checkbox"
                                        defaultValue={1}
                                        defaultChecked="checked"
                                        name="notifications"
                                    />
                                    {/*end::Input*/}
                                    {/*end::Label*/}
                                    <span className="form-check-label text-muted fs-6">
                                        Recuring
                                    </span>
                                    {/*end::Label*/}
                                    </label>
                                    {/*end::Switch*/}
                                </div>
                                </div>
                                {/*end::Menu item*/}
                            </div>
                            {/*end::Menu sub*/}
                            </div>
                            {/*end::Menu item*/}
                            {/*begin::Menu item*/}
                            <div className="menu-item px-3 my-1">
                            <a href="#" className="menu-link px-3">
                                Settings
                            </a>
                            </div>
                            {/*end::Menu item*/}
                        </div>
                        {/*end::Menu 3*/}
                        </div>
                        {/*end::Menu*/}
                    </div>
                    {/*end::Actions*/}
                    </div>
                    {/*end::Head*/}
                    {/*begin::Info*/}
                    
                    {/*end::Info*/}
                </div>
                {/*end::Wrapper*/}
                </div>
                {/*end::Details*/}
                <div className="separator" />
                {/*begin::Nav*/}
                <ul className="nav nav-stretch nav-line-tabs nav-line-tabs-2x border-transparent fs-5 fw-bold">
 
  <li className="nav-item">
    <a
      className={`nav-link text-active-primary me-6 ${activeTab === 'descriptionmonitoring' && 'active'}`}
      onClick={(e) => handleTabChange('descriptionmonitoring', e)} // Pass the event object
      href="#"
    >
      Description
    </a>
  </li>
  <li className="nav-item">
    <a
      className={`nav-link text-active-primary me-6 ${activeTab === 'listmonitoring' && 'active'}`}
      onClick={(e) => handleTabChange('listmonitoring', e)} // Pass the event object
      href="#"
    >
      List
    </a>
  </li>
  <li className="nav-item">
    <a
      className={`nav-link text-active-primary me-6 ${activeTab === 'settings' && 'active'}`}
      onClick={(e) => handleTabChange('settings', e)} // Pass the event object
      href="#"
    >
      Settings
    </a>
  </li>
 
  <li className="nav-item">
    <a
      className={`nav-link text-active-primary me-6 ${activeTab === 'subscription' && 'active'}`}
      onClick={(e) => handleTabChange('subscription', e)} // Pass the event object
      href="#"
    >
      Subscription
    </a>
  </li>
</ul>

                {/*end::Nav*/}
            </div>

            </div>
            
      {activeTab === 'descriptionmonitoring' && <Descriptionmonteary />}
      {activeTab === 'listmonitoring' && <UsersListWrapper/>}
      {activeTab==='settings'&& <SettingsMonitoring/>}
      {activeTab=='subscription' && <PricingMonitoring/>}

            
            </div>
            </div>
            </div>
            </div>

        </>
    )
}
export default PluginPageSample ;